* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden; /* borde de las imagenes no se salgan del contenedor*/
  margin: 0 10px 10px 0;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}